
.section-reviews{
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
    @media screen and (min-width: 768px) {
        padding-left: 74px;
        padding-right: 74px;
    }
    @media screen and (min-width: 1280px) {
        padding-left: 229px;
        padding-right: 229px;
    }
}

.section-reviews__text{
    font-weight: 500;
    font-size: 12px;
    line-height: 2.55;
    margin-bottom: 52px;
    margin-top: 29px;
    color: $txt-black;
    position: relative;
    z-index: 3;
    background-image: repeating-linear-gradient(to top, #E1E1E1 0px 1px, transparent 1px 31px);
    @media screen and (min-width: 768px) {
        font-size: 14px;
        line-height: 2.16;
        letter-spacing: 0.04em; 
    }
    @media screen and (min-width: 1280px) {
        font-size: 16px;
        line-height: 1.86;
    }
}
.section-reviews__text-info{
    font-weight: 500;
    font-size: 14px;
    line-height: 1.86;
    color: $txt-black;
    padding-bottom: 128px;
    @media screen and (min-width: 768px) {
        font-size: 16px;
    }
    @media screen and (min-width: 1280px) {
        font-size: 20px;
    }

}
.section-reviews__img{
    width: 85px;
    height: 85px;
    margin-left: auto;
    margin-right: auto;
}
.section-reviews__wrap::before {
    content: "";
    position: absolute;
background-image: url(../images/quotation-mark.svg);
    width: 54px;
    height: 40px;
    z-index: 1;
    left: 70px;
    top: 55px;
   }
   .section-reviews__wrap{
    padding: 43px 66px 26px 57px;
    position: relative;
   }
